	module.exports = {"data":{"markdownRemark":{"html":"<h1>Contribution Guidelines</h1>\n<h2>Naming Blocks</h2>\n<p>Components, layouts, and demos (blocks) should be in individual folders named using Pascal case (AaaBbb). This is the name that will appear in the navigation of the workspace.\nExample: <code class=\"language-text\">Button</code>, <code class=\"language-text\">SecondaryNav</code></p>\n<h3>Component Example</h3>\n<p>Examples require a parent javascript class in the component/examples folder (PatternFly uses React for building the example application). These files should be named index.js.</p>\n<p>The example component will refer to example handlebars files. These should be named with kebab-case, including the block name plus the example name: <code class=\"language-text\">component-name-type-of-example.hbs</code></p>\n<p>Inside of the React component, we use JavaScript naming standards, naming the imports with Pascal case and properties or constants with camelCase.</p>\n<p>For example:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code class=\"language-html\">import BoxBasicExample from \"./box-basic-example.hbs\";\n...\nconst boxBasicExample = BoxBasicExample();\n...\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Example</span> <span class=\"token attr-name\">heading</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>Box with no footer<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>{boxNoHeaderExample}<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Example</span><span class=\"token punctuation\">></span></span>;</code></pre>\n      </div>\n<h3>Handlebars names</h3>\n<p>The main handlebars file for a block should be named using kebab case. For example, the secondary navigation would be made up of <code class=\"language-text\">secondary-nav.hbs</code> with elements defined in <code class=\"language-text\">secondary-nav-item.hbs</code> and <code class=\"language-text\">secondary-nav-link.hbs</code>.</p>\n<h3>Modifier parameter</h3>\n<p>Every block and element should have a parameter allowing for modifier classes to be passed in. These should be named in kebab case with the block/element name plus <code class=\"language-text\">--modifier</code>.\nFor example:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code class=\"language-html\"><span class=\"token comment\">&lt;!-- Component definition --></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>pf-c-card {{ pf-c-card--modifier }}<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  {{> @partial-block}}\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span>\n---\n<span class=\"token comment\">&lt;!-- Using the component in handlebars --></span>\n{{#> card pf-c-card--modifier=\"pf-m-3xl pf-m-margin\"}}\n  {{> @partial-block}}\n{{/card}}</code></pre>\n      </div>\n<p>When including a partial within a partial, by default, Handlebars will pass along the parent context to it's children. This would mean the value of any property specified by the parent is also used by the children.</p>\n<p>If there is a possibility of a block nested inside another block of the same type and you want to isolate that nested block, add a new context. For example - see how the nested box is defined below with 'newcontext' added as an attribute:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code class=\"language-html\">{{#> card pf-c-card--modifier=\"pf-m-3xl pf-m-margin\"}}\n  {{> @partial-block}}\n  {{#> card newcontext pf-c-card--modifier=\"pf-m-3xl pf-m-margin\"}}\n    {{> @partial-block}}\n  {{/card}}\n{{/card}}</code></pre>\n      </div>\n<h2>Common Modifier Class Names</h2>\n<p>Modifier classes help us to create variations of blocks. Reuse names as much as possible to avoid confusion.</p>\n<table>\n<thead>\n<tr>\n<th>Modifier Class Name</th>\n<th>Outcome</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code class=\"language-text\">pf-m-gutter</code></td>\n<td>Adds vertical (if applicable) and horizontal gutters to the element</td>\n</tr>\n</tbody>\n</table>","fields":{"path":"/contribution","type":"page","contentType":"page"}}},"pathContext":{"pagePath":"/contribution","type":"page","contentType":"page"}}

